:root{
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  min-width: 10vw;
  max-width: 100vw;
}

.App h2{
  margin: 10px auto;
}

.App-link {
  color: #61dafb;
}

.App .nav {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  overflow: hidden;
  border: 1px dotted red;
  max-width: 90%;
  margin: auto;
  border-radius: 10px;
  position: relative;
  z-index: 1;
  padding: 5px 0;
}

/* Adding the border animation */
@property --angle{ 
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
  /* using property to add more details to the vairable, like the syntas: <angle> shich is "animable"*/
}

.sppining-border {
  position: relative;
}

.sppining-border::after, .sppining-border::before{
  content: '';
  position: absolute;
  height: 100%;
  width: 90%;
  background-image: conic-gradient(from var(--angle), #ff4545, #00ff99, #006aff, #ff0095, #ff4545);
  z-index: 0;
  top: 50%;
  translate: -50% -50%;
  padding: 3px;
  border-radius: 10px;
  animation: 3s spin linear infinite;
}

.sppining-border::before{
  filter: blur(1rem);
  opacity: 0.5;
}

@keyframes spin{
  from{
    --angle: 0deg;
  }
  to{
    --angle: 360deg
  }
}

.nav li{
  text-decoration: none;
  list-style: none;
  padding: 0 1rem;
}

.nav li:hover{
  transform: scale(1.2) rotate(-5deg);
  transition: 1s;
}

.nav li a{
  text-decoration: none;
  list-style: none;
  color: aliceblue;
  text-shadow: -3px 3px 5px red;
}

.nav li a:visited {
  color: rgb(41, 123, 196);
}

.nav li a:hover {
  text-shadow: -5px 5px 7px red;
  transition: 1s;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
